export const carList = (type : any) => {
    const type1 = ['(DE) EV','EV3','EV6',' 5 EV ()', ' 6'];
    const type2 = [' EV',' EV'];

    switch (type) {
        case '01' :
            return type1;
        case '02' :
            return type2;
    }
}
